* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* background-color:  black; */
}

.container-fluid {
  width: 90%;
  margin: auto;
}

.table-responsive {
  max-height: 700px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
}

tr:hover {
  background-color: lightgrey;
}

td,
th {
  position: relative;
}

.table td,
.table th {
  padding: 0.75rem;
  vertical-align: top;
  border-top: none;
}

td:hover::after,
th:hover::after {
  content: "";
  position: absolute;
  background-color: #f2f2f2;
  left: 0;
  top: -5000px;
  width: 100%;
  z-index: -1;
}

.table tr td:hover:nth-child(4) {
  background-color: rgba(231, 83, 115);
  color: aliceblue;
  border: 0.5px solid rgba(12, 12, 12);
}

.table tr td:hover{
    background-color: rgba(117, 223, 122);
    color: aliceblue;
    border: 0.5px solid rgba(12, 12, 12);
}


.thead-dark{
    font-weight: bold;
    text-transform: uppercase;
}